import React from "react";
import { BrowserRouter, Route } from "react-router-dom";

import "./App.css";
import Main from "./Main";
import Projects from "./Projects";

function App() {
  return (
    <BrowserRouter>
        <Route exact path="/" component={Main}/>
        <Route path="/projects" component={Projects}/>
    </BrowserRouter>
  );
}

export default App;
